/* poppins-200 - latin */
@font-face {
    font-family: "Poppins200";
    font-style: normal;
    font-weight: 200;
    src: url("./poppins-v15-latin-200.woff2") format("woff2"), url("./poppins-v15-latin-200.woff") format("woff");
}
